<template>
    <div class="hotConsult">
      <div class="title clearfix" >
          <span class="fl" >热门资讯</span>
          <router-link class="fr more" to="/newsList?type=1"> 更多 <span class="el-icon-arrow-right"></span></router-link>
      </div>
      <div class="list">
          <router-link  :to="'/consultDetail?id='+item.id" class="item clearfix" v-for="(item,index) in data" :key="'hot'+item.id" >
            <div class="order fl">{{index+1}}</div>
            <div class="name fl">{{item.title}}</div>
<!--            <div class="count fr">{{item.read_num}}</div>-->
          </router-link>
      </div>
    </div>
</template>

<script>
    import commonApi from "@/api/common-api"
    export default {
        name: "hotConsult",
        data(){
          return {
            data: ''
          }
        },
        mounted () {
          this.getData();
        },
        methods:{
          getData(){
            commonApi.getHotConsult().then( data => {
              this.data = data;
            });
          }
        }
    }
</script>

<style lang="less" scoped type="text/less">
  .hotConsult{
    width: 295px;
   min-height: 420px;
    background-color: #fff;
    .title{
      height: 50px;
      line-height: 50px;
      font-size: 14px;
      font-weight: bold;
      padding: 0 19px;
      border-bottom: 1px solid #E3E4E5;
      .more{
        color: #999;
        font-size: 12px;
        font-weight: normal;
      }
    }
  }
  .list{
    padding:13px 20px 23px 20px;
    .item{
      padding-top:10px;
      display: block;
      .order{
        width: 18px;
        height: 21px;
        text-align: center;
        line-height: 21px;
        font-size: 12px;
        color: #999;
      }
      .name{
        width: 89%;
        font-size: 14px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        margin-left: 10px;
      }
      .count{
        color: #999;
        font-size: 12px;
      }
    }
    .item:nth-child(-n+3) .order{
      color: #fff;
      background: url("../../static/image/icon_hotConsult.jpg") 100% 100% no-repeat;
    }
  }

</style>



