import httpUtils from 'config/index'

class consultDetail {
  getData(params) {
    return httpUtils.get("/index/article/art_detail",params);
  }
  getCommentList(params){
    return httpUtils.get("/index/comment/get_comment_list",params);
  }
  addComment(params){
    return httpUtils.get("/index/comment/add_comment",params);
  }
  addFollow(params){
    return httpUtils.get("/index/follow/add_follow",params);
  }
  removeFollow(params){
    return httpUtils.get("/index/follow/remove_follow",params);
  }
  addLike(params){
    return httpUtils.get("/index/comment/add_likes",params);
  }
}
export default new consultDetail()



