<template>
    <div class="consultDetail">
      <Search></Search>
      <div class="commonNav">
        <div class="wrapper">
          <div class="part">
            <router-link class="active" to="">
              <span>首页</span>
              <i></i>
            </router-link>
            <router-link to="/list">
              <span>找项目</span>
              <i></i>
            </router-link>
            <router-link to="/rankingList">
              <span>排行榜</span>
              <i></i>
            </router-link>
            <router-link to="/news">
              <span>行业资讯</span>
              <i></i>
            </router-link>
          </div>
        </div>
      </div>
      <div class="wrapper clearfix">
        <div class="crumbs">您的位置：行业咨询 > 加盟咨询</div>
        <div class="article fl">
          <div class="title">{{data.title}}</div>
          <div class="subhead between">
            <div>作者：{{data.username}} &nbsp;&nbsp; 发布时间：{{data.addtime}} &nbsp;&nbsp; 阅读量：{{data.read_num}}</div>
            <span class="collectBtn" :class="{'collectActive':data.is_fow==1}" @click="addFollow">
              <i class="iconfont icon-collect"></i>
              <b v-if="data.is_fow==1">已收藏</b>
              <b v-else>收藏</b>
            </span>
          </div>
          <div class="content" v-html="data.detail"></div>
          <div class="footer ">
              <div class="fl">
                <span>上一篇：</span>
                <router-link :to="'/consultDetail?id='+data.prev.id">{{data.prev.title}}</router-link>
              </div>
              <div class="fr">
                <span>下一篇：</span>
                <router-link :to="'/consultDetail?id='+data.next.id">{{data.next.title}}</router-link>
              </div>
          </div>
        </div>
        <HotConsult class="fr"></HotConsult>
      </div>
      <div class="wrapper">
        <div class="articleConsult ">
          <div class="title">
            文章评论
          </div>
          <div class="content">
            <textarea cols="30" rows="10" placeholder="请输入评论内容" v-model="content"></textarea>
            <div class=" clearfix">
              <el-button type="primary" class="fr" @click="addComment">提交评论</el-button>
            </div>
          </div>
          <div class="consult" v-if="commentList.length>0">
            <div class="count">
              共{{commentList.length}}条评论
            </div>
            <div class="consultItem clearfix" v-for="(item,index) in commentList">
              <img class="avatar fl" :src="item.userAvatar" alt="">
              <div class="info fl">
                <div class="name">
                  {{item.username}}
                  <div class="fr noPraise" data-id="item.id" @click="addLike($event,item.id)">
                    <span class="iconfont icon-praise"></span> {{item.likes}}
                  </div>
                </div>
                <div >{{item.content}}</div>
                <div class="reply" v-if="item.comment.length>0">
                  <div v-for="(a,index) in item.comment">{{a.username}}：{{a.content}}</div>
                </div>
                <div class="time clearfix">
                  <span class="fl">{{item.time}}</span>
                  <!--<span class="fr replyBtn">回复</span>-->
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
  import Search from '@/components/Search'
  import HotOrder from '@/components/HotOrder'
  import HotConsult from "@/components/HotConsult"
  import IndustryList from '@/components/IndustryList'
  import api from "@/api/consultDetail-api"
  export default {
    name: "consultDetail",
    data() {
      return {
        data:{},
        commentList:[],
        id:'',
        content:''
      }
    },
    created(){
      console.log("created");
      const {id} = this.$route.query;
      this.id = id;
      this.getData();
      api.getCommentList({id}).then(data => {
        this.commentList = data.data;
        console.log(data.data)
      });
    },
    beforeRouteUpdate(to,from,next){

      const {id} = to.query;
        this.id = id;
        this.getData();
        api.getCommentList({id}).then(data => {
          this.commentList = data.data;
          console.log(data.data)
        });
      next();
    },
    methods:{
      getData(){
        const {id} = this;
        api.getData({id}).then(data => {
          this.data = data;
          console.log(data)
        });

      },
      addFollow:function(){
        const {is_fow,id} = this.data;
        console.log('is_fow:'+is_fow)
        if(is_fow==0){
          api.addFollow({id,type:3}).then(data => {
            this.$message.success('收藏成功');
            this.getData();
          });
        }else{
          api.removeFollow({id,type:3}).then(data => {
            this.$message.success('已取消收藏');
            this.getData();
          });
        }
      },
      addComment:function(){
        const {data,content} = this;
        if(content==''){
          this.$message.error('请输入您要评论的内容');
          return
        }
        api.addComment({topic_id:data.id,to_uid:data.uid,content,pid:0}).then(data => {
          this.getData();
          this.content=''
        });
      },
      addLike:function(e,id){
        api.addLike({id:id}).then(data => {
          this.getData();
        });
      }
    },
    components:{
      Search,
      HotOrder,
      IndustryList,
      HotConsult
    }
  }
</script>

<style lang="less" type="text/less" scoped>
  .consultDetail{
    background-color: #F4F5FA;
    padding-bottom: 125px;
  }
  /* tabbar */
  .commonNav {
    width: 100%;
    height: 40px;
    line-height: 40px;
    background: #2F333B;
    font-size: 16px;
    color: #fff;
    .all{
      float: left;
      width: 240px;
      height: 40px;
      text-align: center;
      background: #51575D;
      img,label{
        display: inline-block;
        vertical-align: middle;
      }
      img{
        margin:-2px 14px 0 0;
      }
    }
    .part{
      a{
        float: left;
        width: 160px;
        height: 40px;
        position: relative;
        color: #fff;
        text-align: center;
      }
      a.active{
        background: #1A1D21;
        i{
          width: 34px;
          height: 2px;
          background: -webkit-gradient(linear, 100% 0, 0 0, from(#579EFA), to(#4F74F4));
          background: -webkit-linear-gradient(to bottom, #579EFA, #4F74F4);
          background: -moz-linear-gradient(to bottom, #579EFA, #4F74F4);
          background: -o-linear-gradient(to bottom, #579EFA, #4F74F4);
          background: linear-gradient(to bottom, #579EFA, #4F74F4);
          position: absolute;
          left: 50%;
          margin-left: -17px;
          bottom: 2px;
          z-index: 1;
        }
      }
    }
    .issueBtn{
      float: right;
      color: #FEAB17;
    }
  }
  /* tabbar */
  /* 面包屑 */
  .crumbs{
    color: #999;
    font-size: 14px;
    line-height: 60px;
  }
  /* 面包屑 */
  /* 文章 */
  .article{
    width: 895px;
    background-color: #fff;
    padding-top:  41px;
    .title{
      font-weight: bold;
      font-size: 22px;
      padding: 0 41px;
    }
    .subhead{
      font-size: 12px;
      color: #999;
      padding: 25px 41px;
    }
    .collectBtn{
      width: 78px;
      height: 28px;
      line-height: 28px;
      text-align: center;
      border:1px solid #E3E4E5;
      border-radius:2px;
      cursor: pointer;
      i,b{
        display: inline-block;
        vertical-align: middle;
        font-weight: normal;
      }
      i{
        margin: -2px 5px 0 0;
        font-size: 18px;
        color: #C8C9CC;
      }
    }
    .collectActive{
      color: #5389F7;
      i{
        color: #5389F7;
      }
    }
    .content{
      min-height: 160px;
      padding: 0 41px 30px 41px;
      border-bottom: 1px solid #E3E4E5;
      line-height: 26px;
    }
    .footer{
        height: 90px;
        line-height: 90px;
        font-size: 14px;
        color: #4D4D4D;
        padding: 0 41px;
        span{
          font-weight: bold;
        }
    }
  }
  /* 文章 */
  /* 文章评论 */
  .articleConsult{
    width: 895px;
    background-color: #fff;
    margin-top: 20px;
    .title{
      height: 22px;
      font-size: 18px;
      font-weight: bold;
      padding: 20px 40px;
      background-color: #F0F7FF;
    }
    .content {
      padding: 32px 40px;
      textarea{
        width: 771px;
        height: 90px;
        padding: 20px;
        border: 1px solid #E3E4E5;
        resize:none;
        margin-bottom: 23px;
      }
    }
    .consult{
      padding: 0 41px;
      .count{
        font-size: 14px;
        line-height: 30px;
        color: #999;
        border-bottom: 1px solid #E3E4E5;
      }
      .consultItem{
        padding: 30px 0;
        .avatar{
          width: 56px;
          height: 56px;
          border-radius: 50%;
        }
        .info{
          width: 730px;
          font-size: 14px;
          line-height: 30px;
          color: #999;
          margin-left: 27px;
          .name{
            color: #333;
            font-weight: bold;
            font-size: 16px;
            .noPraise{
              font-weight: normal;
              span{
                font-size: 20px;
                position: relative;
                top: 1px;
              }
              font-size: 14px;
              color: #C8C9CC;
              cursor: pointer;
            }
          }
          .reply{
            color: #808080;
            padding: 9px 23px;
            background-color: #F4F5FA;
            margin-top: 9px;
          }
          .time{
            margin-top: 9px;
            .replyBtn{
              color: #5389F7;
              text-decoration: underline;
              cursor: pointer;

            }
          }
        }
      }

    }
  }
  /* 文章评论 */
</style>



